import { createClient } from "@supabase/supabase-js";

export const SUPABASE_URL = "https://sb.aidirector.io.vn";
export const SUPABASE_ANON_KEY = "sb_publishable_ACJWlzQHlZjBrEguHvfOxg_3BJgxAaH";
export const supabase = createClient(SUPABASE_URL, SUPABASE_ANON_KEY, {
  auth: {
    persistSession: true,
    detectSessionInUrl: true,
    autoRefreshToken: true,

    //  REMOVE storageKey completely
    // storageKey: SUPABASE_STORAGE_KEY,

    storage:
      typeof window !== "undefined" ? window.localStorage : undefined,
  },

  global: {
    fetch: async (url, options: any = {}) => {
      //  Skip Supabase Auth endpoints
      if (
        typeof url === "string" &&
        (url.includes("/auth/v1/token") ||
          url.includes("/auth/v1/signup") ||
          url.includes("/auth/v1/logout"))
      ) {
        return fetch(url, options);
      }

      try {
        const { data } = await supabase.auth.getSession();
        const token = data?.session?.access_token;
        if (token) {
          options.headers = {
            ...(options.headers || {}),
            Authorization: `Bearer ${token}`,
          };
        }
      } catch (e) {
        console.warn(" Could not attach auth header:", e);
      }

      options.headers = {
        "Content-Type": "application/json",
        ...(options.headers || {}),
      };

      return fetch(url, options);
    },
  },
});
