import React, { useState, useEffect } from "react";
import { Eye, EyeOff, Sparkles, Mail, Lock, X } from "lucide-react";
import "../../styles/auth.css";
import { useLanguage } from "../../i18n/LanguageContext";

import { UniversalLink as Link } from "../../database/UniversalLink";
import { useUniversalRouter } from "../../database/routerViteGatsbyUniversal";

import { supabase } from "../../database/supabase";

export function Login() {
  const { go } = useUniversalRouter();
  const { t } = useLanguage();

  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [showPassword, setShowPassword] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState("");
  const [showToast, setShowToast] = useState(false);
  const [resendLoading, setResendLoading] = useState(false);

  useEffect(() => {
    if (showToast) {
      const timer = setTimeout(() => {
        setShowToast(false);
      }, 6000);
      return () => clearTimeout(timer);
    }
  }, [showToast]);

  const handleResendConfirmation = async () => {
    setResendLoading(true);
    await new Promise(resolve => setTimeout(resolve, 1000));
    setResendLoading(false);
    alert('Confirmation email sent!');
  };

  const handleUpdateEmail = () => {
    setShowToast(false);
    go('/signup');
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError("");
    setIsLoading(true);

    try {
      const { data, error } = await supabase.auth.signInWithPassword({
        email,
        password,
      });

      if (error) {
        setError(error.message || "Invalid email or password.");
        return;
      }

      if (!data?.session) {
        setError("Login failed. No session returned.");
        return;
      }

      go("/app");
    } catch (err: any) {
      setError(err?.message || "Something went wrong. Please try again.");
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="auth-container">
      <div className="auth-bg">
        <div className="auth-gradient-orb orb-1"></div>
        <div className="auth-gradient-orb orb-2"></div>
        <div className="auth-gradient-orb orb-3"></div>
      </div>

      <Link to="/" className="auth-back-link">
        <svg width="20" height="20" viewBox="0 0 20 20" fill="none">
          <path
            d="M15 10H5M5 10L10 15M5 10L10 5"
            stroke="currentColor"
            strokeWidth="2"
            strokeLinecap="round"
            strokeLinejoin="round"
          />
        </svg>
        {t("auth.backHome")}
      </Link>

      <div className="auth-card">
        <div className="auth-header">
          <div className="auth-icon-badge">
            <Sparkles className="auth-sparkle-icon" size={32} />
          </div>
          <h1 className="auth-title">{t("auth.login.title")}</h1>
          <p className="auth-subtitle">{t("auth.login.subtitle")}</p>
        </div>

        <form onSubmit={handleSubmit} className="auth-form">
          {error && <div className="auth-error">{error}</div>}

          <div className="auth-field">
            <label htmlFor="email" className="auth-label">
              <Mail size={14} />
              {t("auth.emailLabel")}
            </label>
            <div className="auth-input-wrapper">
              <input
                id="email"
                type="email"
                className="auth-input"
                placeholder="you@example.com"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                required
                disabled={isLoading}
                autoComplete="email"
              />
            </div>
          </div>

          <div className="auth-field">
            <label htmlFor="password" className="auth-label">
              <Lock size={14} />
              {t("auth.passwordLabel")}
            </label>
            <div className="auth-input-wrapper">
              <input
                id="password"
                type={showPassword ? "text" : "password"}
                className="auth-input"
                placeholder=""
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                required
                disabled={isLoading}
                autoComplete="current-password"
              />
              <button
                type="button"
                className="auth-toggle-password"
                onClick={() => setShowPassword(!showPassword)}
                disabled={isLoading}
                aria-label="Toggle password visibility"
              >
                {showPassword ? <EyeOff size={18} /> : <Eye size={18} />}
              </button>
            </div>
          </div>

          <div style={{ textAlign: "right", marginTop: "-12px" }}>
            <Link
              to="/forgot-password"
              className="auth-link"
              style={{ fontSize: "0.875rem" }}
            >
              {t("auth.forgotPasswordLink")}
            </Link>
          </div>

          <button type="submit" className="auth-submit-btn" disabled={isLoading}>
            {isLoading ? (
              <>
                <span className="auth-spinner"></span>
                {t("auth.login.submitting")}
              </>
            ) : (
              <>
                <Sparkles size={18} />
                {t("auth.login.submit")}
              </>
            )}
          </button>
        </form>

        <div className="auth-footer">
          {t("auth.login.footer").split("?")[0]}?{" "}
          <Link to="/signup" className="auth-link">
            {t("auth.login.footer").split("?")[1]?.trim() || t("common.signup")}
          </Link>
        </div>
      </div>

      {/* Toast Notification */}
      {showToast && (
        <div className="toast-notification">
          <div className="toast-content">
            <div className="toast-message">
              <span>Email not confirmed. Please check your inbox or:</span>
            </div>
            <div className="toast-actions">
              <button 
                className="toast-action-btn primary"
                onClick={handleResendConfirmation}
                disabled={resendLoading}
              >
                {resendLoading ? 'Sending...' : 'Resend confirmation'}
              </button>
              <button 
                className="toast-action-btn secondary"
                onClick={handleUpdateEmail}
              >
                Update email
              </button>
            </div>
          
          </div>
        </div>
      )}
    </div>
  );
}